<template>
    <div id="table" v-if="estoques">
        <div id="table-heading">
            <div>NF</div>
            <div>Chegadas</div>
            <div>Filial</div>
            <div>NF Devolução</div>
            <div>Cliente</div>
            <div>Observação</div>
            <div>Ações</div>
        </div>
        <div id="cadastro-table">
            <div id="cadastro-table-itens" v-for="estoque in estoques" :key="estoque.id">
                <div>{{ estoque.devolucao }}</div>
                <div>{{ estoque.nf }}</div>
                <div>{{ estoque.chegada }}</div>
                <div>{{ estoque.nf_Devolucao }}</div>
                <div>{{ esoque.cliente }}</div>
                <div>{{ estoque.observacao }}</div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name: "Movimentados",
        data() {
        return {
            estoques: null,
        }
    },
    async getEstoque() {
        const req = await fetch('http://localhost:3000/estoque')
        const data = await req.json()
        this.estoques = data
    },
    mounted() {
        this.getEstoque()
    },
}
</script>
